import React from 'react';
import '../../style/exp.css';

class CenterText extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      coeff: window.innerHeight / 4,
      initial: null,
      text: null,
      section: null,
    };
  }

  componentDidMount = () => {
    this.setState({initial: this.props.initial, text: this.props.text, section: this.props.section})
    window.addEventListener('scroll', () => this.transition());
  };

  transition () {
    let update = document.getElementById(this.props.section);
    if (window.pageYOffset < window.innerHeight * this.state.initial) {
      update.style.display = "none";
    } else if (window.pageYOffset < window.innerHeight * (this.state.initial + 0.25)) {
      let diff = window.pageYOffset - window.innerHeight * this.state.initial;
      update.style.display = "flex";
      update.style.opacity = diff / this.state.coeff;
      update.style.transform = `translate(-50%, -${30 + diff / this.state.coeff * 20}%)`;
    } else if (window.pageYOffset < window.innerHeight * (this.state.initial + 0.75)) {
      update.style.opacity = 1;
      update.style.transform = `translate(-50%, -50%)`;
    } else if (window.pageYOffset < window.innerHeight * (this.state.initial + 1.25)) {
      let diff = window.pageYOffset - window.innerHeight * (this.state.initial + 1);
      update.style.display = "flex";
      update.style.opacity = 1 - diff / this.state.coeff;
      update.style.transform = `translate(-50%, -${70 + diff / this.state.coeff * 20}%)`;
    } else {
      update.style.display = "none";
    };
  }

  render() {
    return (
      <div className="center-wrapper" id={this.state.section}>
        <h1 className="big-text">
          {this.state.text}
        </h1>
      </div>
    )
  };
};

export default CenterText;