import React from 'react';
import About from './Components/About/index.js';
import CenterText from './Components/CenterText/index.js';
import Exp from './Components/Experience/index.js';
import Frost from './Components/FrostedGlass/index.js';
import InProgress from './Components/InProgress/index.js';
import ProfileImg from './Components/ProfileImg/index.js';
import RefreshMsg from './Components/RefreshMsg/index.js';
import ScrollMsg from './Components/ScrollMsg/index.js';
import Skills from './Components/Skills/index.js';
import './style/title.css';
import './style/intro.css';
import './style/exp.css';
import 'animate.css';
import './App.css'

class App extends React.Component {

  componentDidMount = () => {
    window.addEventListener('scroll', () => {
      if (window.pageYOffset < window.innerHeight / 2) {
        document.getElementById("back").style.filter = `blur(${window.pageYOffset / (window.innerHeight/2) * 15}px)`;
      } else {
        document.getElementById("back").style.filter = "blur(15px)";
      }
      if (window.pageYOffset < window.innerHeight * 1.5) {
        document.getElementById("intro").style.backgroundColor = "rgb(0, 0, 0, 0.25)";
      } else if (window.pageYOffset < window.innerHeight * 2) {
        let coeff = window.pageYOffset - window.innerHeight * 1.5;
        document.getElementById("intro").style.backgroundColor = `rgb(0, 0, 0, ${coeff / (window.innerHeight * 0.667) + 0.25})`;
      } else {
        document.getElementById("intro").style.backgroundColor = "rgb(0, 0, 0, 1)";
      }
    });
  };

  render() {
    return (
      <div className="root" id="root">
        <div className="background animate__animated animate__fadeIn animate__delay-1s" id="back"></div>
        <div className="title-back">
          <RefreshMsg/>
          <Frost/>
          <ScrollMsg/>
        </div>
        <div className="intro animate__animated animate__fadeIn animate__delay-2s" id="intro">
          <ProfileImg/>
          <About/>
          <Skills/>
          <Exp/>
        </div>
      </div>
    );
  }
}

export default App;
