import React from 'react';
// import Small from '../../media/small-circle.svg'
// import Big from '../../media/big-circle.svg'
import '../../../style/exp.css';

class Circles extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      coeff: window.innerHeight / 4,
      initial: 1.9,
    };
  }

  componentDidMount = () => {
    window.addEventListener('scroll', () => this.transition());
  };

  transition () {
    let update = document.getElementById("glow-sphere");
    let progress = window.pageYOffset - window.innerHeight * this.state.initial;
    console.log(0.8 + progress / this.state.coeff * 0.2);
    if (window.pageYOffset < window.innerHeight * this.state.initial) {
      update.style.display = "none";
    } else if (window.pageYOffset < window.innerHeight * (this.state.initial + 0.25)) {
      update.style.display = "flex";
      update.style.opacity = progress / this.state.coeff;
      update.style.transform = `translate(-50%, -50%) 
                                rotate(${progress / this.state.coeff * 60}deg) 
                                scale(${0.7 + progress / this.state.coeff * 0.07})`;
    } else if (window.pageYOffset < window.innerHeight * (this.state.initial + 0.75)) {
      update.style.opacity = 1;
      update.style.transform = `translate(-50%, -50%)
                                rotate(${progress / this.state.coeff * 60}deg)
                                scale(${0.7 + progress / this.state.coeff * 0.07})`;
    } else if (window.pageYOffset < window.innerHeight * (this.state.initial + 1.35)) {
      let diff = window.pageYOffset - window.innerHeight * (this.state.initial + 1.1);
      update.style.display = "flex";
      update.style.opacity = 1 - diff / this.state.coeff;
      update.style.transform = `translate(-50%, -50%)
                                rotate(${progress / this.state.coeff * 60}deg)
                                scale(${0.7 + progress / this.state.coeff * 0.07})`;
    } else {
      update.style.display = "none";
    };
  }

  render() {
    return (
      <div>
        <div className="glow-sphere" id="glow-sphere"></div>
      </div>
    )
  };
};

export default Circles;